.container{
    width: 90%;
    height: 250vh;
    background-color: #0e1b32;
  
    .wrapper{
      position: relative;
      left: 30px;
  
      .titleRecommendation {
        width: 90%;
      }
    }
  
    .overviewContent{
      display: flex;
    }
  
    .detail{
      margin: 3% 0 0 2%;
      color: white;
      font-size: 21px;
  
      .data{
        position: relative;
        margin: 30px 0 0 10px;
      }
    }
    .title{
      padding: 10px;
      margin: 10px 0 10px 0;
    }
  
    .img{
      position: relative;
      top: 40px;
      height: 400px;
      width: 300px;
      margin: 3% 0 0 3%;
      box-shadow: 0px 0px 17px 28px rgba(10,42,168,0.57);  
      border-radius: 12px;
    }
  
    .average{
      position: relative;
    }
  
    .overview {
      padding: 0px 10px 0px 10px;
    }
  
    .vote_average{
      font-size: 23px;
      padding: 10px 10px 0px 10px;
    }
  
    .reviews{
      display: flex; 
      flex-direction: column;
      margin-top: 35px; 
      margin-bottom: 15px;
  
      .detailsReviews{
        display: block;
        font-size: 21px;
        color: white;
      }
  
      .reviewsTitle{
        font-size: 28px;
        color: gray;
        margin-bottom: 10px;
      }
  
      .author{ 
        font-size: 21px;
        color: red;
      }
  
      .content{
        color: white;
      }
    }
  }

@media screen and (max-width: 720px) {
  .container{
    display: flex;
    flex-direction: column;

    .wrapper{
      display: flex;
      flex-direction: column;
      justify-content: center;
      .overviewContent{
        font-size: 12px;
        display: flex;
        flex-direction: column;
        
        .imgwrapper{
          border: 1px solid white;
          .img{
            display: flex;
            justify-content: center;
            align-items: center;
            width: 150px;
            height: 250px;
            object-fit: cover;
            margin-left: auto;
            margin-right: auto; 
          }

          .average{
            left: 7em;
          }
        }

        .detail{
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          font-size: 17px;
          
          .title{
            font-size: 17px;
          }
        }
      }
    }
  }
}
